*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
    background-image: url('./img/3dwhite.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    background-attachment: fixed;
}

/*Navbar section*/

#navbar-container{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    z-index: 1;
    background-color: white;
}
#navbar-link-container li a {
    border-bottom: 3px solid transparent;
}
#navbar-link-container li a:active{
    background-color: blue;

}
#navbar-link-container li a:hover {
    border-bottom: 3px solid blue;
}
#open-menu{
    cursor: pointer;
}

/*mobile*/

#navbar-container-two{
    height: 100vh;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: -40px;
    background-color:#333;
    overflow: hidden;
    transition: 0.5s;
}
#navbar-link-container-two{
    margin-top: -300px;
}
#close-menu{
   margin-left: 250%;
   cursor: pointer;
   top: 0;
   right: 0;
}

/*home section*/
#mypic{
    height:400px;
    width:300px;
    border-top-right-radius: 500px;
    border-bottom-right-radius: 500px;
    border-bottom-left-radius:500px;
    object-fit: cover;
}

/*education section*/

.card-img-top img{
    object-fit: contain;
}

/*skills*/

#html{
    display: flex;
    height: 40px;
    background-color: white;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}
@media screen and (max-width:567px) {
    #navbar-link-container li {
        display: none;
    }
    #mypic{
        height: 300px;
        width: 200px;
        margin: auto;
        display: block;
    }
    #html-container{
        margin-top: 10px;
    }
    #lastname{
        margin-top: 10px;
    }
}